#product{
    max-width: 1200px;  
     display: flex;
     flex-wrap: wrap;   
     padding: 2% 8%;
     margin:0 auto;
     box-sizing: border-box;
   
 }
 .left-column {
     width: 40%;
     margin-top: 20px;
     padding-right: 8%;      
     box-sizing: border-box;   
    
 }
 #productImg {
     width: 100%;   
     box-sizing: border-box;
 }
 .right-column {    
     width: 60%;     
     box-sizing: border-box;
     z-index: 5;
     text-align: start;
 }
 #name {
     font-size: 48px;
     font-weight: 500;
     margin: 0;
 }
 #brand {
     font-weight: 600;
     color: black;
     opacity: 0.6;
     margin: 0;
     margin-top: 8px;
 }
 .product-description h3{
     font-size: 24px;
     color: rgba(0, 0, 0, 0.8);
     margin-top: 20px;
     font-weight: 400;
     margin-bottom: 16px;
 }
 .product-description h1{
     letter-spacing: -2px;
     line-height: 1.2;
 }
 #price {
     max-width: 500px;
     font-size: 24px;
     letter-spacing: 1px;
    
     font-weight: 600;
     color: #009688;
     margin: 0;
     display: inline-block;
 }
 #description {
     max-width: 500px;
     font-size: 16px;
     letter-spacing: 1px;    
     font-weight: 400;
     color: rgba(0, 0, 0, 0.6);
 }
 .previewImg{
     width:100%;
     display:flex;
     cursor: pointer;
 }
 .previewImg img {
     width: 50px;
     margin: 8px;
     padding:4px;
     border-radius: 6px;
     border: 4px solid transparent;
 }
 .previewImg .active{    
     border: 2px solid #009688;
 }
 #add-to-cart{
     background-color: #009688;
     font-size: 18px;
     border: 0;
     cursor: pointer;
     outline: none;
     padding: 18px 36px;
     border-radius: 6px;
     margin-top: 36px;
     font-weight: 600;    
     text-transform:uppercase;
     color: white;
     transition: transform 200ms;
 }
 .bigger {
     transform: scale(1.1, 1.1);
 }